export default function FoodPic() {
  const imageLink = [
    "taco.png",
    "breakfast-burrito.png",
    "burritos.png",
    "churros.png",
    "enchiladas.png",
    "mexican-rice.png",
    "nachos.png",
    "salad-bowl.png",
    "tamales.png",
  ];

  return (
    <>
      {imageLink.map((link, i) => (
        <img
          key={i}
          src={`${process.env.PUBLIC_URL}/images/${link}`}
          alt="main"
        />
      ))}
    </>
  );
}
