import { useRef } from "react";
import "../styles/BodyWrap.scss";

export default function BodyWrap({ children }) {
  const ref = useRef();

  return (
    <div ref={ref} id="body__wrap">
        <div id="bg">
          <h1>almighty Taco</h1>
          {children}
        </div>
    </div>
  );
}
