import { translateToppings } from "../helper";

export default function SetToppings({ location, handleCheck, foodName, menu }) {
  const toppings = translateToppings(location);

  return (
    <div className={menu === "beverages" ? "toppings prevent" : "toppings"}>
      <h6>add toppings</h6>
      {toppings.map(({ value }, i) => (
        <div key={i}>
          <input
            value={value}
            id={foodName + i}
            type="checkbox"
            onChange={handleCheck}
          />
          <label htmlFor={foodName + i}>{value}</label>
        </div>
      ))}
    </div>
  );
}
