import React, { useContext } from "react";
import { Context } from "../Context";
import { totalPrice } from "../helper";
import "../styles/Cart.scss";

export default function Cart({ location }) {
  const { order, deleteItem } = useContext(Context);

  return (
    <div className="recipt">
      <p className="total">
        {location.includes("korea") ? "합계 : " : "TOTAL : $"}
        {order && 0 + totalPrice(Array.from(order, ({ price }) => price))}
        {location.includes("korea") ? "원" : ""}
      </p>
      <div className="reciptWrap">
        {order &&
          order.map((item, index) => (
            <div className="myFood" key={index}>
              <span className="index">{index + 1}</span>
              <div>
                <h2>{item.food.name}</h2>
                <p className="topping">
                  toppings :
                  {item.topping === undefined || item.topping.length === 0 ? (
                    <span> none</span>
                  ) : (
                    item.topping.map((t, index) => (
                      <span key={index}> {t},</span>
                    ))
                  )}
                </p>
              </div>
              <span className="qty">{item.qty}</span>
              <span className="price">
                {location.includes("korea")
                  ? item.price + "원"
                  : "$" + item.price}
              </span>
              <span
                className="delete"
                onClick={() => {
                  deleteItem(index);
                }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="red"
                  strokeWidth="2"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M15 12H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
                  />
                </svg>
              </span>
            </div>
          ))}
      </div>
    </div>
  );
}
