import { useContext, useEffect, useState } from "react";
import { useNavigate, useLocation, useSearchParams } from "react-router-dom";
import { translateBeverage, translateMain } from "../helper";
import { Context } from "../Context";
import Food from "../components/Food";
import Cart from "../components/Cart";
import "../styles/Order.scss";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/all";
import BodyWrap from "../components/BodyWrap";
import Loading from "../components/Loading";

export default function Order() {
  gsap.registerPlugin(ScrollTrigger);
  const { order } = useContext(Context);
  const navigate = useNavigate();
  const storeLocation = useLocation().pathname;
  const [searchParams] = useSearchParams();
  const menu = searchParams.get("menu");
  const mainData = translateMain(storeLocation);
  const beveragesData = translateBeverage(storeLocation);
  const [load, setLoad] = useState(false);
  const [openCart, setOpenCart] = useState(false);
  const goToCart = () => {
    setOpenCart(!openCart);
  };

  const setMenuURL = (menu) => {
    navigate(`${storeLocation}?menu=${menu}`);
  };

  const goToBack = () => {
    if (window.location.search.includes("Cart")) {
      navigate(navigate(`${storeLocation}?menu=${menu}`));
    } else {
      gsap
        .timeline()
        .to("#bg", {
          xPercent: 200,
          onComplete: () => setLoad(true),
        })
        .to("#bg", {
          opacity: 0,
          xPercent: -200,
          duration: 0,
        })
        .to("#bg", {
          opacity: 1,
          duration: 0,
        })
        .to("#bg", {
          opacity: 1,
          xPercent: 0,
          onComplete: () => {
            navigate(`/`);
            window.location.reload();
          },
        });
    }
  };
  useEffect(() => {
    const tl = gsap.timeline();

    tl.fromTo("#menuWrap", { xPercent: 0 }, { xPercent: -200 });
    ScrollTrigger.create({
      animation: tl,
      ease: "none",
      trigger: "body",
      start: "top top",
      pin: true,
      scrub: 1,
      anticipatePin: 1,
    });
  }, []);
  return (
    <BodyWrap>
      {load ? (
        <Loading />
      ) : (
        <>
          <div className="btns">
            <button
              onClick={() => {
                setMenuURL("main");
              }}
              className={menu === "main" ? "click" : ""}
            >
              {storeLocation === "/korea" ? "음식" : "Main menu"}
            </button>
            <button
              onClick={() => {
                setMenuURL("beverages");
              }}
              className={menu === "beverages" ? "click" : ""}
            >
              {storeLocation === "/korea" ? "음료" : "beverages"}
            </button>
            <button className={openCart ? "clickCart" : ""} onClick={goToCart}>
              cart {order.length}
            </button>
          </div>

          <Food
            data={menu === "main" ? mainData : beveragesData}
            location={storeLocation}
          />
          <span onClick={goToBack} className="backBtn">
            &#10161;
          </span>
          {openCart ? <Cart location={storeLocation} /> : ""}
        </>
      )}
    </BodyWrap>
  );
}
