import SetQty from "./SetQty";
import SetToppings from "./SetToppings";
import { Context } from "../Context";
import { useState, useContext } from "react";
import { translateAlert } from "../helper";

export default function SetOrder({ food, location, menu, index }) {
  const [topping, setTopping] = useState([]);
  const [qty, setQty] = useState(0);
  const { updateItem } = useContext(Context);

  const handleCheck = (e) => {
    var updatedList = [...topping];
    if (e.target.checked) {
      updatedList = [...topping, e.target.value];
    } else {
      updatedList.splice(topping.indexOf(e.target.value), 1);
    }
    setTopping(updatedList);
  };

  let inputs = document.querySelectorAll(".toppings");
  const Order = (e) => {
    let input = inputs[e.target.id].querySelectorAll("input[type=checkbox]");
    if (qty > 0) {
      updateItem(food, qty, topping);
      setQty(0);
      setTopping([]);
      input.forEach((el) => (el.checked = false));
    } else {
      translateAlert(location);
      input.remove();
    }
  };

  return (
    <form>
      <SetQty qty={qty} setQty={setQty} />
      <SetToppings
        location={location}
        handleCheck={handleCheck}
        foodName={food.name}
        menu={menu}
      />
      <button id={index} type="reset" onClick={Order}>
        {location === "/korea" ? "주문" : "ADD TO ORDER"}
      </button>
    </form>
  );
}
