import React, { Suspense, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import "../styles/Store.scss";
import Select from "react-select";
import FoodPic from "../components/FoodPic";
import { gsap } from "gsap";
import BodyWrap from "../components/BodyWrap";
import Loading from "../components/Loading";

export default function Store() {
  const [store, setStore] = useState("");
  const selectRef = useRef();
  const circleRef = useRef();
  const navigate = useNavigate();
  const [load, setLoad] = useState(false);
  const goToStore = () => {
    gsap
      .timeline()
      .to("#bg", {
        xPercent: -200,
        onComplete: () => setLoad(true),
      })
      .to("#bg", {
        opacity: 0,
        xPercent: 100,
        duration: 0,
      })
      .to("#bg", {
        opacity: 1,
        duration: 0,
      })
      .to("#bg", {
        opacity: 1,
        xPercent: 0,
        onComplete: () =>
          store === ""
            ? selectRef.current.focus()
            : navigate(`${store}?menu=main`),
      });
  };
  const handleChange = (event) => {
    setStore(event.value);
  };
  const stores = [
    { value: "korea", label: "Korea" },
    { value: "mexico", label: "Mexico" },
    { value: "usa", label: "Usa" },
  ];
  useEffect(() => {
    const q = gsap.utils.selector(circleRef);
    const repeat = () => {
      tl.play(1);
    };
    let tl = gsap.timeline({ onComplete: repeat });
    const images = q("img");

    images.forEach((pic, i) => {
      tl.to(pic, {
        opacity: 1,
        duration: 1,
        repeat: 1,
        repeatDelay: 1,
        yoyo: true,
      });

      if (i === images.length - 1) {
        tl.to(images[0], { duration: 1, opacity: 1 });
      }
    });
  }, []);

  return (
    <Suspense fallback={<Loading />}>
      <BodyWrap>
        {load ? (
          <Loading />
        ) : (
          <>
            <div className="circle" ref={circleRef}>
              <FoodPic />
            </div>
            <div className="choose__store">
              <Select
                className="select"
                placeholder="Please pick store to Order"
                options={stores}
                ref={selectRef}
                onChange={handleChange}
              />
              <button onClick={goToStore}>Go grab taco</button>
            </div>
          </>
        )}
      </BodyWrap>
    </Suspense>
  );
}
