import { useEffect, useRef } from "react";
import { gsap } from "gsap/all";
import { useSearchParams } from "react-router-dom";
import SetOrder from "./SetOrder";
const array = ["korea", "mexico", "usa"];

export default function Food({ data, location }) {
  const refs = useRef();
  const [searchParams] = useSearchParams();
  const menu = searchParams.get("menu");
  const Pathname = window.location.pathname;

  useEffect(() => {
    let q = gsap.utils.selector(refs);
    if (array.some((s) => Pathname.includes(s))) {
      gsap
        .timeline({ delay: 0.5 })
        .to(q(".cardWrap"), { opacity: 1, stagger: 0.1 });
    }
  }, [Pathname]);

  return (
    <div ref={refs} id="menuWrap">
      {data.map((food, index) => (
        <div className="cardWrap" key={index}>
          <h3>{food.recommend === "yes" ? "🔥Top 3 menu" : ""}</h3>
          <div className="imge">
            <img src={food.image} alt="foodpic" />
          </div>
          <span className="foodname">{food.name}</span>
          <div className="price">
            {location.includes("korea") ? (
              <span>{food.price}원</span>
            ) : (
              <span>${food.price}</span>
            )}
          </div>
          <SetOrder food={food} location={location} menu={menu} index={index} />
        </div>
      ))}
    </div>
  );
}
